import axios from 'axios'
import qs from 'qs'

export default{
	get_record(h,p,o,n,c){
		axios.post('https://hdapi.codeclassroom.org/index/hd/record',
			qs.stringify({
				hd_id:h,
				parentopenid:p,
				openid:o,
				cz_name:n,
				centre_id:c,
			}))
			.then(res => {
				if(res.data.status == 2){
					alert(res.data.msg)
				}
			})
			.catch(err => {
				console.log(err)
			})
	}
}