export default{
    getWechat(c,h,o){
		var link = "https://wx.codeclassroom.org/activity/index.php?parent_openid=" + o + "&centre_id=" + c + "&hdid=" + h;
		var uurl = location.href.split('#')[0];
		$.ajax({
			type: "post",
			url: "https://hdapi.codeclassroom.org/index/hd/shares",
			dataType: "json",
			async: false,
			data: {
				url: uurl
			},
			success: function(data) {
				wx.config({
					debug: false, //debug
					appId: data.result.appId, //appId
					timestamp: data.result.timestamp, //
					nonceStr: data.result.nonceStr, //
					signature: data.result.signature, //
					jsApiList: [ //JS
						'checkJsApi', //JS
						'onMenuShareTimeline', //
						'onMenuShareAppMessage', //
						'getLocation',//
					]
				});
			}
		})
		wx.ready(function() {
			var protocol = window.location.protocol;
			//
			$.ajax({
				"url": "https://hdapi.codeclassroom.org/index/hd/fenx",
				type: "post",
				dataType: 'json',
				data: {
					centre_id: c,
					hd_id: h
				},
				success: function(data) {
					var imgurl = data.data.img
					var intro = data.data.fx_describe
					var share_title = data.data.title
		
					wx.onMenuShareTimeline({
						title: share_title,
						desc: intro, // 
						link: link,
						imgUrl: imgurl, // 
						trigger: function(res) {
							// triggerajaxajax.
						},
						success: function(res) {
							$.ajax({
								"url": "https://hdapi.codeclassroom.org/index/hd/fxgzt",
								type: "post",
								dataType: 'json',
								data: {
									openid: o,
									centre_id: c,
									hd_id: h
								},
								success: function(data) {
									
								}
							});
						},
						cancel: function(res) {
							//alert('shared cancle');
						},
						fail: function(res) {
							//alert(JSON.stringify(res));
						}
					});
					//
					wx.onMenuShareAppMessage({
						title: share_title,
						desc: intro, // 
						link: link,
						imgUrl: imgurl, // 
						type: 'link', // ,musicvideolinklink
						dataUrl: '', // typemusicvideo
						success: function(res) {
							// 
							$.ajax({
								"url": "https://hdapi.codeclassroom.org/index/hd/fxgzt",
								type: "post",
								dataType: 'json',
								data: {
									openid: o,
									centre_id: c,
									hd_id: h
								},
								success: function(data) {
									
								},
								error: function(e) {
									
								}
							});
						},
						cancel: function(res) {
							// 
						},
						fail: function(res) {
							//alert(JSON.stringify(res));
						}
					});
				}
			});
			wx.getLocation({
				type: 'wgs84', // wgs84gpsopenLocation'gcj02'
				success: function(res) {
					var latitude = res.latitude; // 90 ~ -90
					var longitude = res.longitude; // 180 ~ -180
					var speed = res.speed; // /
					var accuracy = res.accuracy; // 
		
					$.ajax({
						"url": "https://hdapi.codeclassroom.org/index/hd/check_user_address",
						type: "post",
						dataType: 'json',
						data: {
							openid: o,
							hd_id: h,
							latitude: latitude,
							longitude: longitude
						},
						success: function(data) {
							
						}
					});
				},
				error: function(e) {
					alert(e.errMsg);
				},
				cancel: function(res) {
					alert('');
				}
			});
			wx.checkJsApi({
				jsApiList: [
					'getLocation'
				],
				success: function (res) {
					if (res.checkResult.getLocation == false) {
						alert('JS');
					}
				}
			}); 
			wx.error(function(res) {
				alert(':' + res.errMsg);
			});
		});
	}
}